import './App.css';

import Navigation from './components/Navigation/Navigation';
import ColumnChart from "./containers/column-chart/Column-chart";
import DoubleSlider from "./containers/double-slider/DoubleSlider";
import PageNotFound from "./containers/notfound/PageNotFound";
import Notification from "./containers/notification/Notification";
import ProductForm from "./containers/product-form/ProductForm";
import RangePicker from "./containers/range-picker/RangePicker";
import SortableTable from "./containers/sortable-table/SortableTable";
import Tooltip from "./containers/tooltip/Tooltip";

import { Route, Switch } from "react-router";

function App() {
  return (
    <div className="App">
       <Navigation/>
        <main>
            <Switch>
                <Route exact path="/column-chart" component={ColumnChart} />
                <Route exact path="/notification" component={Notification} />
                <Route exact path="/product-form" component={ProductForm} />
                <Route exact path="/range-picker" component={RangePicker} />
                <Route exact path="/sortable-table" component={SortableTable} />
                <Route exact path="/tooltip" component={Tooltip} />
                <Route exact path="/double-slider" component={DoubleSlider} />
                <Route exact path="/" render={() => (
                    <h1>React Training</h1>
                )} />
                <Route component={PageNotFound} />
            </Switch>
        </main>
    </div>
  );
}

export default App;
